
const state = {
  closekey: 'w',
  slides: []
}

const mutations = {
  SET_CLOSE_KEY:(state,key) => {
    state.closekey = key
  },
  CLEAR_SLIDES:(state,key) => {
    state.slides = key
  }
}

const actions = {
  setCloseKey({commit}, key) {
    commit('SET_CLOSE_KEY', key)
  },
  setSlides({commit}, key) {
    commit('CLEAR_SLIDES', key)
  },
}

export default {
  namespaced: true,
  state,
  mutations,
  actions
}
